import React, { useState, useEffect, useRef } from 'react';

// import ProductsList from '../base_components/products_list';
import { PurchaseRecordsLogisticRequestGet } from './purchase_records_logistic_requests';
// import CreatePurchaseRecordsLogistic from './create_purchase_records_logistic';
import UpdatePurchaseRecordsLogistic from './update_purchase_records_logistic';
// import { ItemRendererComponent } from './purchase_records_logistic_columns_label_config';
import { PurchaseRecordsListComponent } from '../base_components/purchase_records_goods_delivery_address/purchase_records_list';

// 列表编辑 页面

const PurchaseRecordsLogisticList = ({ reloadListCount, setReloadListCount, requestParams }) => {
  const hasMore = useRef(true);
  const [hasMoreState, setHasMoreState] = useState(true);
  const [loading, setLoading] = useState(false);
  const reqParams = useRef({ page: 0 });

  const [purchaseRecordsLogisticData, setPurchaseRecordsLogisticData] = useState([]);
  const [purchaseRecordsLogisticRequestGetCount, setPurchaseRecordsLogisticRequestGetCount] = useState(0);

  // const [createPurchaseRecordsLogisticVisible, setCreatePurchaseRecordsLogisticVisible] = useState(false);
  const [updatePurchaseRecordsLogisticVisible, setUpdatePurchaseRecordsLogisticVisible] = useState(false);
  const [selectedObj, setSelectedObj] = useState({});

  useEffect(() => {
    // 加载、更新或创建数据后 重新读取列表
    reqParams.current = { ...requestParams };
    reloadListsHandle();
  }, [reloadListCount]);

  useEffect(() => {
    // useState才可以读取更多
    setHasMoreState(hasMore.current);
  }, [hasMore.current])

  const loadMore = () => {
    // 读取更多列表
    if (hasMore.current && !loading) {
      const current_page = Number.isInteger(reqParams.current.page) ? reqParams.current.page + 1 : 1;
      reqParams.current = { ...reqParams.current, page: current_page };
      console.log(reqParams.current);
      setPurchaseRecordsLogisticRequestGetCount(purchaseRecordsLogisticRequestGetCount + 1);
      setLoading(true);
    } else {
      console.log('Error state: hasMore is ', hasMore.current, ', loading is ', loading);
    }
  };

  const reloadListsHandle = () => {
    // 重新读取数据
    reqParams.current = { ...reqParams.current, page: 0 };
    // console.log(reqParams.current);
    setPurchaseRecordsLogisticData([]);
    // setHasMore(true);
    hasMore.current = true;
    setLoading(false);
    setTimeout(() => {
      loadMore();
      console.log('end loadMore...');
    }, 100);
  };

  const judgeHasMore = resp => {
    if (Object.keys(resp).includes('next')
      && typeof resp.next === 'string'
      && resp.next.length > 5) {
      return true;
    }
    return false;
  };

  const purchaseRecordsLogisticGetResponse = resp => {
    console.log('resp process...', resp);
    setLoading(false);
    if (Object.keys(resp).includes('code')
      && resp.code === 1000
      && Object.keys(resp).includes('results')) {
      setPurchaseRecordsLogisticData(purchaseRecordsLogisticData.concat(resp.results));
      // setHasMore(judgeHasMore(resp));   // 判断是否还有更多
      hasMore.current = judgeHasMore(resp);
    } else {
      console.log('Error: get data...');
    }
  };

  const itemDetailClick = (index) => {
    // 打开详情编辑窗口
    // console.log(purchaseRecordsLogisticData[index]);
    let selObj = { ...purchaseRecordsLogisticData[index] };
    console.log(selObj);
    // 日期转 moment 时间
    // selObj.end_time = moment(dateFormat(selObj.end_time), 'YYYY-MM-DD HH:mm:ss');
    setSelectedObj(selObj);
    setUpdatePurchaseRecordsLogisticVisible(true);
  };

  // const CurrentItemRendererComponent = ({ item, index }) => {
  //   // 每一行渲染 的组件
  //   return (
  //     <ItemRendererComponent item={item} index={index} itemDetailClick={itemDetailClick} />
  //   )
  // }

  return (
    <div>
      <PurchaseRecordsLogisticRequestGet requestCount={purchaseRecordsLogisticRequestGetCount}
        reqParams={reqParams.current}
        responseDataHandle={purchaseRecordsLogisticGetResponse}></PurchaseRecordsLogisticRequestGet>
      {/* <ProductsList
        productsData={purchaseRecordsLogisticData}
        loadMore={loadMore}
        hasMore={hasMoreState}
        ItemRendererComponent={CurrentItemRendererComponent}></ProductsList> */}

      <PurchaseRecordsListComponent
        purchaseRecordsList={purchaseRecordsLogisticData}
        loadMore={loadMore}
        hasMore={hasMoreState}
        loading={loading}
        infoBottonTips="快递发货"
        infoBottonClick={itemDetailClick}
        customBottonTips="电子面单打印"
      ></PurchaseRecordsListComponent>

      <UpdatePurchaseRecordsLogistic
        visible={updatePurchaseRecordsLogisticVisible}
        setVisible={setUpdatePurchaseRecordsLogisticVisible}
        oldObj={selectedObj}
        reloadListCount={reloadListCount}
        setReloadListCount={setReloadListCount}
      ></UpdatePurchaseRecordsLogistic>
    </div>
  );
}

PurchaseRecordsLogisticList.defaultProps = {
  reloadListCount: 1,
  setReloadListCount: (addedNum) => { console.log(addedNum); },
  requestParams: { page: 0 }
}

export default PurchaseRecordsLogisticList;
